.import {
  display: flex;
  flex-direction: row;
  margin-bottom: 1rem;
}

.options {
  z-index: 1000;
  border: 1px solid black;
  border-radius: 0.5rem;
  padding: 0.5rem;
  position: fixed;
  top: 1rem;
  background-color: white;
}

.filename {
  display:flex;
  align-items:center;
  font-family: monospace;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.help p,
.help h2 {
  margin: 0.5rem;
  width: 30vw;
}

kbd {
  border: 1px solid black;
  border-radius: 0.2rem;
  background-color: lightgray;
}

.copyright {
  position: fixed;
  bottom: 0;
  right: 0;
  background-color: lightgray
}